.card {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 350px;
    height: 630px;
    margin: 10px;
    border-radius: 10px;
    -webkit-box-shadow: 3px 3px 1px 0px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 3px 3px 1px 0px rgba(0, 0, 0, 0.5);
    box-shadow: 3px 3px 1px 0px rgba(0, 0, 0, 0.5);
  }
  .card .icon {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    color: #fff700;
  }
  .card img {
    border-radius: 10px 10px 0px 0px;
    width: 100%;
    max-height: 530px;
  }
  .card .info {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }
  .card .info .title {
    font-size: 22px;
    text-align: center;
  }
  .card .info .year {
    font-size: 25px;
    font-weight: bold;
  }